<template>
  <div class="fade" @click.self="close()" ref="fade">
    <div class="modal">

      <div class="cancel-parent">
        <button @click.stop="close(undefined)" class="btn-cancel">
          <svg aria-hidden="true" focusable="false" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><path d="M193.94 256L296.5 153.44l21.15-21.15c3.12-3.12 3.12-8.19 0-11.31l-22.63-22.63c-3.12-3.12-8.19-3.12-11.31 0L160 222.06 36.29 98.34c-3.12-3.12-8.19-3.12-11.31 0L2.34 120.97c-3.12 3.12-3.12 8.19 0 11.31L126.06 256 2.34 379.71c-3.12 3.12-3.12 8.19 0 11.31l22.63 22.63c3.12 3.12 8.19 3.12 11.31 0L160 289.94 262.56 392.5l21.15 21.15c3.12 3.12 8.19 3.12 11.31 0l22.63-22.63c3.12-3.12 3.12-8.19 0-11.31L193.94 256z" ></path></svg>
        </button>
      </div>

      <img ref="preview" src="" alt="image" />
    </div>
  </div>
</template>

<style lang='scss' scoped>

.fade {
  display: none;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: #0000009c;
  align-items: center;
  justify-content: center;
  z-index: 9999999;
  cursor: zoom-out;

  .modal {
    position: relative;
    cursor: auto;
    max-width: 90vw;
    max-height: 90vh;
    overflow: hidden;

    .cancel-parent {
      display: inline-block;
      width: 40px;
      height: 40px;
      position: absolute;
      top: 10px;
      right: 10px;

      .btn-cancel {
        font-weight: bold;
        border: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        background: #fff;
        outline: none;
        width: 40px;
        height: 40px;
        position: relative;
        cursor: pointer;
        display: block;
        border: 1px solid #ccc;

        svg {
          height: 26px; 
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
              
          path {
            fill: #666;
          }
        }
      }
    }
  }
}
</style>

<script>
export default {
  computed: {
    imagePreview: function() {
      return this.$store.state.imagePreview
    }
  },
  watch: {
    imagePreview: function(src, prev) {
      if (src) {
        this.$refs.preview.src = src;
        
        this.$refs.fade.style.display = "flex";
        document.body.style.overflow = "hidden";
      }
    }
  },
  methods: {
    close() {
      this.$refs.fade.style.display = "none";
      document.body.style.overflow = "auto";
      this.$store.commit('setImagePreview', undefined)
    }
  }
}
</script>