<script>
const mapMarkers = [];

export default {
  props: {
    google: {
      type: Object,
      required: true
    },
    map: {
      type: Object,
      required: true
    },
    polygon: {
      type: Object,
      required: true
    }
  },
  mounted () {
    const { Polygon, LatLng } = this.google.maps

    const geometries = this.polygon.data[0]['geojson']['coordinates'];
    let polygonPaths = [];
    
    if (geometries) {
        for (var m in geometries) {
            for (var n in geometries[m]) {
                polygonPaths.push(
                    new LatLng(geometries[m][n][1], geometries[m][n][0]));
            }
        }

        const countryPolygon = new Polygon({
            paths: polygonPaths,
            strokeColor: this.polygon.color,
            strokeOpacity: 1,
            strokeWeight: 1.7,
            fillColor: this.polygon.color,
            fillOpacity: 0.5,
            iso2Digit: "DE",
            cursor: 'default'
        });

        countryPolygon.setMap(this.map);
    }
  },
  render () {}
}
</script>
