<template>
  <div v-if="activeView === 'table' || activeView === 'map'">
    <div class="landscape-menu">
      <div
        v-for="item in landscapes"
        :key="item.id"
        class="menu-point"
        :class="{active: item.id === activeLandscape}"
        :title="item.name"
        @click="changeActiveLandscape(item.id)"
        @mouseenter="toggleInfo(true, item.description)"
        @mouseleave="toggleInfo(false)">

        <img alt='icon' :src="item.icon" />

        <div class="menu-title">
          {{ item.name }}
        </div>
      </div>
    </div>
    <div class="landscape-info" ref="landscapeInfo">
      {{ landscapeInfo }}
    </div>
  </div>
</template>

<script>

export default {
  name: 'LandscapeMenu',
  data: function() {
    return {
      landscapeInfo: ""
    }
  },
  computed: {
    activeView () {
      return this.$store.state.activeView
    },
    landscapes () {
      return this.$store.state.landscapes
    },
    activeLandscape () {
      return this.$store.state.activeLandscape
    },
    activeDestinations: function () {
      return this.$store.getters.activeDestinations
    }
  },
  methods: {
    changeActiveLandscape (id) {
      this.$store.commit('setActiveDestination', undefined)

      if (this.activeLandscape !== id) {
        const view = this.activeView

        if (view === "table") {
          window.scrollTo(0, 0);
        }

        this.$router.replace(`/${view}/${id}`)
        this.$store.commit('setActiveLandscape', id)
      }
    },
    toggleInfo (show, text) {
      if (show && text) {
        this.$refs.landscapeInfo.style.display = "block";
        this.landscapeInfo = text;
      } else {
        this.$refs.landscapeInfo.style.display = "none";
        this.landscapeInfo = "";
      }
    }
  }
}
</script>

