  .landscape-menu {
    z-index: 9999999;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
    align-items: center;
    width: 100%;

    .menu-point {
      border-right: 1px solid #ccc;
      border-bottom: 1px solid #ccc;
      display: grid;
      grid-template-columns: 15% 85%;
      grid-template-rows: 55px;
      align-items: center;
      text-align: left;
      overflow: hidden;

      &.active {
        background-color: #f7d0d0;
        color: #6249d4;
      }

      &:hover {
        cursor: pointer;
        background-color: #f3e9e9;
      }

      img {
        padding: 4px;
      }

      .menu-title {
        font-size: 15px;
        margin-left: 10px;
        line-height: 19px
      }

      svg {
        height: auto;
        width: 100%;
        padding: 4px;
      }
    }
  }

  .landscape-info {
    background: #fff;
    position: fixed;
    display: none;
    top: 56px;
    left: 0;
    width: 100vw;
    z-index: 9999999;
    text-align: center;
    padding: 10px 24px;
    border-bottom: 1px solid #ccc;
  }


</style>
