<script>

import GoogleMapLoader from './GoogleMapLoader'
import GoogleMapMarker from './GoogleMapMarker'
import GoogleMapPolygon from './GoogleMapPolygon'

import { mapSettings } from '../constants/mapSettings'
import { getContentTitle, getContentIcon, getMaterialIcon } from '../constants/utils'

export default {
  name: 'DestinationMap',

  components: {
    GoogleMapLoader,
    GoogleMapMarker,
    GoogleMapPolygon
  },
  computed: {
    activeDestinations: function () {
      return this.$store.getters.activeDestinations
    },
    activeDestination: function () {
      return this.$store.state.activeDestination
    },
    destinations: function () {
      return this.$store.state.destinations
    },
    polygons: function () {
      const polygonsString = this.$store.state.polygons;
      
      return JSON.parse(polygonsString);
    },
    mapConfig: function () {
      return {
        ...mapSettings,
        center: this.mapCenter
      }
    },
    mapCenter () {
      return { lat: 50.825626, lng: 14.386419 }
    },
    activeDestinationStyle: function () {
      if (this.activeDestination.image) {
        return `background-image: url(${this.activeDestination.image})`;
      } else {
        return `height: 39px; cursor: auto;`;
      }
    },
    activeLandscapeMaterials () {
      return this.$store.getters.activeLandscapeMaterials;
    }
  },
  methods: {
    setActiveDestination (destination) {
      this.$store.commit('setActiveDestination', destination)

      const activeLandscape = this.$store.state.activeLandscape
      const activeView = this.$store.state.activeView

      if (destination) {
        const activeDestination = this.$store.state.activeDestination.id
        this.$router.replace(`/${activeView}/${activeLandscape}/${activeDestination}`)
      } else {
        this.$router.replace(`/${activeView}/${activeLandscape}`)
      }
    },
    getContentTitle (type) {
      return getContentTitle(type);
    },
    getContentIcon (type) {
      return getContentIcon(type);
    },
    getMaterialIcon (item) {
      return getMaterialIcon(item);
    },
    imagePreview () {
      if (this.activeDestination.image) {
        let src = this.$refs.imagePreview.style.backgroundImage;
        src = src.substring(5);
        src = src.substring(0, src.length - 2);

        this.$store.commit('setImagePreview', src)
      }
    }
  },
  data () {
    return {}
  }
}
</script>
