<template>
  <div>
    
    <div class="view-nav">
      <button @click="centerMap()">
        <svg aria-hidden="true" focusable="false" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512" class="nav-icon">
          <path fill="currentColor" d="M570.24 247.41L512 199.52V104a8 8 0 0 0-8-8h-32a8 8 0 0 0-7.95 7.88v56.22L323.87 45a56.06 56.06 0 0 0-71.74 0L5.76 247.41a16 16 0 0 0-2 22.54L14 282.25a16 16 0 0 0 22.53 2L64 261.69V448a32.09 32.09 0 0 0 32 32h128a32.09 32.09 0 0 0 32-32V344h64v104a32.09 32.09 0 0 0 32 32h128a32.07 32.07 0 0 0 32-31.76V261.67l27.53 22.62a16 16 0 0 0 22.53-2L572.29 270a16 16 0 0 0-2.05-22.59zM463.85 432H368V328a32.09 32.09 0 0 0-32-32h-96a32.09 32.09 0 0 0-32 32v104h-96V222.27L288 77.65l176 144.56z"></path>
        </svg>
        <span class="tooltip">Zentrieren die Karte</span>
      </button>
    </div>


    <div
      class="google-map"
      ref="googleMap"
    ></div>
    <template v-if="Boolean(this.google) && Boolean(this.map)">
      <slot
        :google="google"
        :map="map"
      />
    </template>
  </div>
</template>

<script>
import GoogleMapsApiLoader from 'google-maps-api-loader'
import MarkerClusterer from '@googlemaps/markerclustererplus';
import { POINT_MARKER_ICON_CONFIG, POINT_MARKER_ICON_CONFIG_DE, POINT_MARKER_ICON_CONFIG_CZ } from '../constants/mapSettings'

export default {
  props: {
    mapConfig: Object,
    apiKey: String
  },

  data () {
    return {
      google: null,
      map: null,
      markers: [],
      cluster: undefined,
      clusterEvent: undefined
    }
  },

  computed: {
    destinations: function () {
      return this.$store.state.destinations
    },
    activeLandscape: function () {
      return this.$store.state.activeLandscape
    },
    activeMarkers: function () {
      return this.markers.filter((location) => {
        return location.landscape === this.activeLandscape;
      });
    }
  },

   watch: {
    activeLandscape: function(n, o) {
      this.markers.map((location) => {
        if (location.marker.landscape === n) {
          location.setMap(this.map);
        } else {
          location.setMap(undefined);
        }
      });
      this.cluster.clearMarkers();
      this.cluster = undefined;
      this.google.maps.event.removeListener(this.clusterEvent);
      this.createCluster();

      this.centerMap();
    }
  },

  async mounted () {
    const googleMapApi = await GoogleMapsApiLoader({
      apiKey: this.apiKey
    })
    this.google = googleMapApi
    this.initializeMap()
    this.createCluster();
  },

  destroyed() {
    const mapCenter = {
      zoom: this.map.getZoom(),
      center: {
        lat: this.map.getCenter().lat(),
        lng: this.map.getCenter().lng()
      }
    }

    this.$store.commit('setMapProps', mapCenter)
  },

  methods: {
    initializeMap () {
      const mapContainer = this.$refs.googleMap
      this.map = new this.google.maps.Map(mapContainer, this.mapConfig)
      this.initMarkers();

      const zoom = this.$store.state.mapZoom
      const center = this.$store.state.mapCenter
      if (zoom && center) {
        this.map.setCenter(center);
        this.map.setZoom(zoom);
      }

    },
    initMarkers () {
      
      const locations =  this.destinations;

      this.markers = locations.map((location, i) => {

        const position = { lat: parseFloat(location.lat), lng: parseFloat(location.lng) };

        let icon = POINT_MARKER_ICON_CONFIG;
        if (location.country) {
          switch (location.country) {
            case "DE":
              icon = POINT_MARKER_ICON_CONFIG_DE;
              break;
            case "CZ":
              icon = POINT_MARKER_ICON_CONFIG_CZ;
              break;
          }
        }

        let map = undefined; 

        if (location.landscape === this.activeLandscape) {
          map = this.map;
        }

        const googleMarker = new this.google.maps.Marker({
          position: position,
          marker: location,
          icon: icon,
          landscape: location.landscape,
          map: map
        });

        googleMarker.addListener("click", (value) => {

          const destination = googleMarker.marker
          this.$store.commit('setActiveDestination', destination)

          const activeLandscape = this.$store.state.activeLandscape
          const activeView = this.$store.state.activeView

          const activeDestination = this.$store.state.activeDestination.id
          this.$router.replace(`/${activeView}/${activeLandscape}/${activeDestination}`)

          var markerPos = googleMarker.getPosition(), center = this.map.getCenter(),
            mapNE = this.map.getBounds().getNorthEast();

          // if(!(markerPos.lng() > center.lng() && markerPos.lng() < mapNE.lng())) { //marker is not on the right side
              //based on center and NE calulate pos to center the marker
              this.map.panTo(new google.maps.LatLng(markerPos.lat(), (markerPos.lng() - (mapNE.lng() - center.lng())/5)));
          // }
         });
        
        return googleMarker;
      });
    },
    createCluster () {

      const styles = [
        {
          width: 30,
          height: 30,
          className: "custom-clustericon-1",
        },
        {
          width: 30,
          height: 30,
          className: "custom-clustericon-2",
        },
        {
          width: 30,
          height: 30,
          className: "custom-clustericon-3",
        }
      ];

      this.cluster = new MarkerClusterer(this.map, this.activeMarkers, {
        styles: styles,
        clusterClass: "custom-clustericon"
      });

      const store = this.$store;
      const router = this.$router;

      this.clusterEvent = this.google.maps.event.addListener(this.cluster, "click", function (c) {
        
        store.commit('setActiveDestination', undefined)

        const activeLandscape = store.state.activeLandscape
        const activeView = store.state.activeView

        router.replace(`/${activeView}/${activeLandscape}`)        
      });
    },
    centerMap () {
      this.map.setZoom(this.mapConfig.zoom);
      this.map.setCenter(this.mapConfig.center);

      this.$store.commit('setActiveDestination', undefined);

      const activeLandscape = this.$store.state.activeLandscape;
      const activeView = this.$store.state.activeView;

      this.$router.replace(`/${activeView}/${activeLandscape}`)        
    }
  }
}
</script>

<style lang="scss" scoped>
.google-map {
  height: calc( 100vh - 55px) ;
  width: 100%;
  min-height: 100%;
  top: 55px;

  button {
    outline: none;
  }
  img {
    filter: blur(0px) !important;
  }

  .cluster {
    outline: none;
    
    div {
      outline: none;
    }
  }
}

.view-nav {
  bottom: 249px;
  right: 10px;
  height: 40px;

  &:hover path {
    fill: #6249d4;
  }
}

</style>
