<script>
import { POINT_MARKER_ICON_CONFIG, POINT_MARKER_ICON_CONFIG_DE, POINT_MARKER_ICON_CONFIG_CZ } from '../constants/mapSettings'

export default {
  props: {
    google: {
      type: Object,
      required: true
    },
    map: {
      type: Object,
      required: true
    },
    marker: {
      type: Object,
      required: true
    }
  },
  data: function () {
    return {
      googleMarker: undefined
    }
  },
  computed: {
    activeLandscape: function() {
      return this.$store.state.activeLandscape
    }
  },
  watch: {
    activeLandscape: function(n, o) {
      if (this.marker.landscape === n) {
        this.googleMarker.setMap(this.map);
      } else {
        this.googleMarker.setMap(undefined);
      }
    }
  },
  mounted () {
    const { Marker, MarkerClusterer } = this.google.maps

    const position = { lat: parseFloat(this.marker.lat), lng: parseFloat(this.marker.lng) };

    let icon = POINT_MARKER_ICON_CONFIG;
    if (this.marker.country) {
      switch (this.marker.country) {
        case "DE":
          icon = POINT_MARKER_ICON_CONFIG_DE;
          break;
        case "CZ":
          icon = POINT_MARKER_ICON_CONFIG_CZ;
          break;
      }
    }
      
    const marker = new Marker({
      position: position,
      marker: this.marker,
      icon: icon,
      landscape: this.marker.landscape
    })


    if (this.marker.landscape === this.activeLandscape) {
      marker.setMap(this.map);
    }

    marker.addListener("click", () => {
      const destination = this.marker
      this.$store.commit('setActiveDestination', destination)

      const activeLandscape = this.$store.state.activeLandscape
      const activeView = this.$store.state.activeView

      if (destination) {
        const activeDestination = this.$store.state.activeDestination.id
        this.$router.replace(`/${activeView}/${activeLandscape}/${activeDestination}`)
      } else {
        this.$router.replace(`/${activeView}/${activeLandscape}`)
      }

    });

    this.googleMarker = marker;
  },
  render () {}
}
</script>
