const translations = {"info_title":"Information zur Seite","info_1_block_title":"Hintergrund","info_1_block_text":"W\u00e4hrend in der tschechisch-s\u00e4chsischen Grenzregion vor einigen Jahren noch eine hohe Arbeitslosigkeit ein zentrales Problem darstellte, werden nun der demographische Wandel, der damit einhergehende Fachkr\u00e4ftemangel und die Sprachbarriere als Hauptherausforderungen gesehen (vgl. SMUL 2020: 86). Somit besteht ein zunehmender Bedarf an Hochschulabsolvent*innen, die sich f\u00fcr eine berufliche Zukunft in der Region entscheiden und neben Fachwissen auch \u00fcber fachsprachliche Kompetenzen verf\u00fcgen. Diese Tatsache muss sich auch im fachsprachlichen Unterricht an Hochschulen sowie  terf\u00fchrenden Schulen widerspiegeln. Genau daran setzt das im Rahmen des vom Europ\u00e4ischen Fonds f\u00fcr regionale Entwicklung gef\u00f6rderte Projekt ProFit im Beruf \u2013 durch Sprach- und Fachsensibilisierung im Studium (ProFit v profesi - jazykovou a odbornou senzibilizac\u00ed b\u011bhem studia) an. Das Kooperationsprojekt zwischen der Technischen Universit\u00e4t Liberec (TUL), Lead-Partner im Projekt, und der Wests\u00e4chsischen Hochschule Zwickau (WHZ) hat das \u00fcbergreifende Ziel, Studierende mit flexibel einsetzbaren Fachkenntnissen aber auch (fach-)sprachlichen und interkulturellen Kompetenzen auszur\u00fcsten. Damit soll die Verbesserung ihrer Chancen f\u00fcr einen erfolgreichen Einstieg in den regionalen, grenznahen Arbeitsmarkt einhergehen. Die dazu entwickelten und durchgef\u00fchrten Projektaktivit\u00e4ten mit dem Ziel einer fachbezogenen und f\u00fcr die Region zugeschnittene Sprachvermittlung waren vielf\u00e4ltig. An dieser Stelle sei auf ein zentrales Zitat von Albert Raasch (2004: 8) aus seinen Reflexionen zu Nachbarsprachen in Grenzregionen verwiesen: \u201eUnterricht braucht Lehrmaterial, das die grenz\u00fcberschreitenden Beziehungen, die Kultur der Region jenseits der Grenze, die identit\u00e4tsstiftende Kooperation thematisiert.\u201c Inwiefern sich die explizite Thematisierung des tschechisch-s\u00e4chsischen Wirtschaftsraums in Fremdsprachenkursen, wie z. B. dem Wirtschaftsdeutschunterricht an Hochschulen in den Nachbarregionen, inhaltlich exemplarisch umgesetzt werden kann, zeigt diese Webseite.\r\n\r\nQuellen: \r\nRaasch, Albert (2004) \u201eEin europ\u00e4isches Projekt: Nachbarsprachen in Grenzregionen. Erfahrungen und Ergebnisse\u201d. In:  Beitr\u00e4ge zum Thematischen Forum \u201aMehrsprachigkeit in Grenzregionen\u2018 (4\/2004). www.eurac.edu\r\nS\u00e4chsisches Staatsministerium f\u00fcr Umwelt und Landwirtschaft (2020): Programmbegleitende Evaluierung zum Kooperationsprogramm Freistaat Sachsen \u2013 Tschechische Republik 2014-2020. In: https:\/\/www.sn-cz2020.eu\/media\/de_cs\/aktuelles\/neue_foerderperiode\/Soziooekonomische_Analyse_Endbericht_20200227_final.pdf","info_2_block_title":"Themen","info_2_block_text":"In gemeinsamer Absprache wurden von den zehn Themenfelder f\u00fcr Erstellung der Lehr- und Lernmaterialien festgelegt.\r\n1. Deutschland u. Tschechien als Import- und Exportl\u00e4nder\r\n2. Regionale Wirtschafts- und Kooperationsbeziehungen und ihre Spezifika\r\n3. Traditionelle Branchen im Liberecer und Zwickauer Gebiet \u2013 Stand und Perspektiven\r\n4. Wirtschaftspotenzial der Grenzregion, u. a. regionale Produkte und Vermarktung\r\n5. Grenz\u00fcberschreitendes Projektmanagement\r\n6. Nachhaltigkeit: Infrastrukturen, Erneuerbare Energien, Elektromobilit\u00e4t, Umwelt\r\n7. Moderne Technologien in der Wirtschaft\r\n8. Lebensqualit\u00e4t und Tourismus als Wirtschaftsfaktoren in der Grenzregion\r\n9. Messen und Ausstellungen\r\n10. Praktisches Projektmanagement im Studium\r\nDie zehn festgelegten Themenfelder wurden in den sechs Landschaften zusammengefasst. Dabei bildet das zugrundeliegende Konzept der \u201eLandschaften\u201c als Metapher die Dynamik von Entwicklungsprozessen in Wirtschaft, Natur und Gesellschaft ab. Die gew\u00e4hlten thematischen Schwerpunkte sollen die an den Kursen teilnehmenden Studierenden sowohl f\u00fcr Fragestellungen und Probleme, aber auch f\u00fcr Innovationen und Probleml\u00f6sungen sensibilisieren, die den Alltag der Menschen und die Berufswelt der Zukunft ma\u00dfgeblich bestimmen werden. Grundlage der \u00dcbungssequenzen zu den verschiedenen Lernorten ist meist ein authentischer Text und\/ oder ein Video. Anhand der Vielfalt der festgelegten Themenbereiche decken dieses ein breites Spektrum an Branchen ab. Die darauf abgestimmten digitalen \u00dcbungen zielen insbesondere darauf ab, das Leseverstehen und H\u00f6r- bzw. H\u00f6rsehverstehen zu trainieren.","info_3_block_title":"Nutzung","info_3_block_text":"Ganz im Sinne des Kennenlernens und n\u00e4heren Besch\u00e4ftigung mit der Region sind die digitalen Lerneinheiten in den einzelnen thematischen \u201eLandschaften\u201c in eine Karte eingebettet, die die Grenzregion abbildet. Die \u201eLernorte\u201c sind meist thematisch verbunden mit einem realen Ort auf der Karte. Beim Erkunden der Karte werden durch das Anklicken eines Lernorts \u00fcber einen Informationsblock jeweils das Sprachniveau der \u00dcbung, die relevanten Sprachhandlungen, das damit verbundene Wortfeld und grammatische Schwerpunktthema transparent gemacht. Des Weiteren sind teilweise auch Links zur weiteren Recherche angegeben. Zudem beinhalten alle Landschaften PDF-Dokumente mit thematisch passenden Unterrichtsvorschl\u00e4gen f\u00fcr Lehrende.","info_4_block_title":"Kontakt","info_4_block_text":"Haben Sie Fragen zu dieser Seite? Oder sind Sie selbst im Sprachlernbereich in der tschechisch-s\u00e4chsischen Grenzregion t\u00e4tig und haben selbst Lern-\/Lehrmaterial entwickelt, das sich auf die Region bezieht? Helfen Sie mit die Landschaften zu bereichern, indem Sie es uns mit Angabe des vorgesehenen Sprachniveaus zusenden. Das kann ein Arbeitsblatt im PDF-Format sein oder auch ein Link zu einer Online-\u00dcbung. Gern pflegen wir Ihr Material in die passende Landschaft ein.\r\n\r\nE-Mail: profitimberuf@gmail.com","btn_level":"Sprachniveau","btn_language_activities":"Sprachhandlungen","btn_vocabulary":"Wortschatz","btn_grammar":"Grammatik","btn_info":"Information","btn_view_map":"Kartenansicht","btn_view_table":"Tabellenansicht","btn_view_search":"Suche","search_title":"Suche:","error_size":"Diese Seite wird nur auf Ger\u00e4ten mit einer Pixelbreite von \u00fcber 1000 Pixel angezeigt. Bitte nutzen Sie ein dementsprechendes Ger\u00e4t.","info_legend_title":"Legende","info_legend_text":"Alle Materialien zeichnen sich durch eine flexible Einsetzbarkeit aus, da sie keiner stringenten sprachlichen Progression folgen und sind als Erg\u00e4nzung und nicht als Ersatz eines kurstragenden Lehrwerks zu sehen. Weitere Hinweise zur Entstehung, Einsatzm\u00f6glichkeiten und Erfahrungen mit dem Material in Hartinger (2021): Wirtschaftsdeutsch regional und digital vermitteln \u2013 Ans\u00e4tze im Rahmen des Projekts \u201eProFit im Beruf \u2013 ProFit v profesi\u201c. In: Busch-Lauer, Ines\/Hartinger, Julia (Hrsg.):  Fachlich \u2013 Digital \u2013 Regional: Perspektiven auf das Sprachenlehren und -lernen. (Studien zu Fach, Sprache & Kultur 8). Berlin: Frank & Timme.\r\n\r\nDie Icons stammen von folgenden Webseiten:\r\nwww.fontawesome.com\r\nwww.flaticon.com","info_legend_view_title":"Ansichten","info_legend_view_map":"Erkunden Sie Lernorte im Kartenmodus.","info_legend_view_table":"Hier bekommen Sie eine \u00dcbersicht aller Lernorte dieser Landschaft.","info_legend_view_search":"Sie suchen \u00dcbungen zu einem bestimmten Sprachniveau, grammtischen Thema, einer Sprachhandhandlung etc.? Dann nutzen Sie die Suchfunktion.","info_legend_map_title":"Kartenoberfl\u00e4che","info_legend_map_zoom":"Vergr\u00f6\u00dferen oder verkleinern Sie die Karte.","info_legend_map_title_home":"Zentrieren Sie die Karte.","info_legend_map_cluster":"Hier befinden sich mehrere Lernorte. Zoomen Sie die Karte n\u00e4her heran.","info_legend_map_point":"Hier befindet sich ein Lernort. Klicken Sie ihn an, um die \u00dcbung zu sehen.","info_legend_map_level":"Die \u00dcbung entspricht folgender Niveaustufe des GER. ","info_legend_map_language_activities":"Diese Sprachhandlung werden in diesem Lernort trainiert.","info_legend_map_vocabulary":"Wortschatz in diesem Themenbereich kann hier ge\u00fcbt werden.","info_legend_map_grammar":"Dieses grammatische Thema k\u00f6nnen Sie in diesem Lernort vertiefen.","info_legend_destination_title":"Lernorte im Detail"};

