    setActiveLandscape (state, id) {
      state.activeLandscape = id
    },
    setActiveDestination (state, destination) {
      state.activeDestination = destination
    },
    resetInitRun (state) {
      state.isInitRun = false
    },
    setActiveView (state, view) {
      state.activeView = view
    },
    setImagePreview (state, src) {
      state.imagePreview = src;
    },
    setMapProps (state, mapCenter) {
      state.mapZoom = mapCenter.zoom;
      state.mapCenter = mapCenter.center;
    },
