</template>

<script>
import LandscapeMenu from '@/components/LandscapeMenu.vue'
import ImagePreview from '@/components/ImagePreview.vue'

export default {
  name: 'app',
  components: {
    LandscapeMenu,
    ImagePreview
  },
  data: function () {
    return {
      mapClass: "",
      tableClass: "",
      searchClass: "",
      displayApp: false
    };
  },
  mounted () {
    if (window.innerWidth >= 1000) {
      this.displayApp = true
    }
    window.addEventListener("resize", this.windowResize);

    const view = this.$store.state.activeView;
    switch (view) {
      case "table": {
        this.tableClass = "active";
        break;
      }
      case "map": {
        this.mapClass = "active";
        break;
      }
      case "search": {
        this.searchClass = "active";
        break;
      }
    }
  },
  methods: {
    changeView (view) {
      const activeView = this.$store.state.activeView;
      if (view === activeView) {
         return;
      }

      this.$store.commit('setActiveView', view)

      const activeLandscape = this.$store.state.activeLandscape
      
      let activeDestination;
      if (this.$store.state.activeDestination) {
        activeDestination = this.$store.state.activeDestination.id
      }
      
      this.tableClass = "";
      this.mapClass = "";
      this.searchClass = "";


      if (this.hasOwnProperty(`${view}Class`)) {
        this[`${view}Class`] = "active";
      }
      
      switch (view) {
        case "map": {
          if (activeDestination) {
            this.$router.replace(`/${view}/${activeLandscape}/${activeDestination}`);
          } else {
            this.$router.replace(`/${view}/${activeLandscape}`);
          }
          break;
        }
        case "table": {
          this.$router.replace(`/${view}/${activeLandscape}`);
          break;
        }
        default: {
          this.$router.replace(`/${view}/`)
          break;
        }
      }
    },
    windowResize () {
      this.displayApp = window.innerWidth >= 1000
    }
  }
}
</script>

