import Vue from 'vue'
import VueRouter from 'vue-router'
import PageMap from '../views/PageMap.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: PageMap
  },
  {
    path: '/map/:landscape_id?/:destination_id?',
    component: PageMap,
    beforeEnter: (to, from, next) => {
      store.commit('setActiveView', "map");
      if (store.state.isInitRun) {
        store.commit('resetInitRun')

        if (to.params.landscape_id) {
          const landscapeId = to.params.landscape_id

          if (store.getters.getLandscapeById(landscapeId)) {
            store.commit('setActiveLandscape', landscapeId)

            if (to.params.destination_id) {
              const destination = store.getters.getDestinationById(to.params.destination_id)

              if (destination && destination.landscape === landscapeId) {
                store.commit('setActiveDestination', destination)
                next()
              } else {
                next(`/map/${landscapeId}`)
              }
            } else {
              next()
            }
          } else {
            next('/map')
          }
        } else {
          next()
        }
      } else {
        next()
      }
    }
  },
  {
    path: '/table/:landscape_id?',
    component: () => import('../views/PageTable.vue'),
    beforeEnter: (to, from, next) => {
      store.commit('setActiveView', "table");
      if (store.state.isInitRun) {
        store.commit('resetInitRun')

        if (to.params.landscape_id) {
          const landscapeId = to.params.landscape_id

          if (store.getters.getLandscapeById(landscapeId)) {
            store.commit('setActiveLandscape', landscapeId)
            next()
          } else {
            next('/table')
          }
        } else {
          next()
        }
      } else {
        next()
      }
    }
  },
  {
    path: '/info',
    component: () => import( '../views/PageInfo.vue'),
    beforeEnter: (to, from, next) => {
      store.commit('setActiveView', "info");
      if (store.state.isInitRun) {
        store.commit('resetInitRun')
        next()
      } else {
        next()
      }
    }
  },
  {
    path: '/search',
    component: () => import( '../views/PageTable.vue'),
    beforeEnter: (to, from, next) => {
      store.commit('setActiveView', "search");
      if (store.state.isInitRun) {
        store.commit('resetInitRun')
        store.commit('setSearchQuery', to.query)
        next()
      } else {
        next()
      }
    }
  },
  {
    path: '*',
    component: PageMap,
    beforeEnter: (to, from, next) => {
      if (store.state.isInitRun) {
        store.commit('resetInitRun')
        next('/map')
      } else {
        next()
      }
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
